$(function(){
    class Accordion{
        constructor(){
            this.accordion()
            $(document).on('click', '.accTitle', this.jqAccordion);
            $(document).on('click', '.accTarget', this.jqAccordionTarget);
            
        }

        accordion() {
            let acc = document.getElementsByClassName("accTitle");

            $.each(acc, function (i, l) {
              
                // calculates on page load 

                if ($(this).hasClass("active")) {
                    let panel = acc[i].nextElementSibling;
                    if (panel.style.maxHeight) {
                        panel.style.maxHeight = null;
                    } else {
                        panel.style.maxHeight = panel.scrollHeight + "px";
                    }
                }
            });

        }

        jqAccordion(){
            const $this = $(this);
            $this.toggleClass('active')
            const content = $this.next()
            const scrollHeight = content.prop('scrollHeight')
            
            if (content.css('maxHeight') !== '0px') {
                content.css('maxHeight', '0px');
            } 
            else {
                content.css('maxHeight', `${scrollHeight}px`);
            }
        }

        jqAccordionTarget() {
            const $this = $(this);
            $this.toggleClass('active')
            const id = $this.attr('data-target')
            
            const scrollHeight = $(`#${id}`).prop('scrollHeight')

            if ($(`#${id}`).css('maxHeight') !== '0px') {
                $(`#${id}`).css('maxHeight', '0px');
            } else {
                $(`#${id}`).css('maxHeight', `${scrollHeight}px`);
            }
            
        }


    }
    new Accordion();
    
})
