
$(function() {
    class AccessibilityWidget {
        constructor() {

            //setting up variables 
            this.rangeInput = $('.text-size__range');
            this.textInput = $('.accessibility__dropdown--radio.text-size input[name="text-size"]');
            this.audioInput = $('.accessibility__dropdown--radio input[name="audio"]');
            this.signInput = $('.accessibility__dropdown--radio input[name="sign"]');
            this.colorChange = $('.accessibility__dropdown--color-input input[name="blind2"]');
            this.accMob = $('.m-acc.md-trigger');
            this.dropdown = $('header .accessibility .header-dropdown');

            //intializing functions
            this.rangeInput.on('input', 'input',function() {accWidget.onRangeInput(this)});
            this.audioInput.on('change', function() {accWidget.onAudioInput(this)})
            this.textInput.on('change', function() {accWidget.onTextSize()})
            this.signInput.on('change', function() {accWidget.onSignInput(this)})
            this.colorChange.on('click', function() {accWidget.onColorChange(this)})

            this.init();

                
        }

        init(){

            //initializing the style sheet to be used by the range slider
            let sheet = document.createElement('style');
            sheet.id = "styleRange"
            this.rangeInput.append(sheet);
            this.sheet = document.getElementById('styleRange');
            this.sheet.textContent = this.setStyle(2);

            let cookie = getCookie("blind-mode");

            //overwritting default state making sure the default is checked
            if( cookie == null || typeof cookie == undefined || cookie == ''){
                $('.accessibility .blind.blind-default').prop('checked', true);
            }else{
                $('.accessibility .blind.'+cookie).prop('checked', true);
            }
        }

        //input change for audio
        onAudioInput(e) {
            $('#audioResult').html(e.value);
        }

        //input change for sign
        onSignInput(e) {
            $('#signResult').html(e.value);
        }

        //On the change of the color 
        onColorChange(e){
            let val = parseInt(this.rangeInput.children('input').val()),
                color = e.getAttribute('data-body-class')
            this.sheet.textContent = this.setStyle(val, color);
           

        }

        //text size slider js
        onTextSize(){
            // reinit reInitAccordion
            this.reInitAccordion()
            setTimeout(()=>{
                this.reInitAccordion()
            },1000)
        }

        onRangeInput(el) {
            const label = $('.text-size__range-result');
            const input = $('.text-size__range input');

            const curVal = parseInt(el.value);
            switch (curVal) {
                case 1:
                    label.html(input.attr('data-small')); //changing label
                    input.attr('data-font', 'small'); // changing font size
                    break;
                case 2:
                    label.html(input.attr('data-medium')); //changing label
                    input.attr('data-font', 'normal'); // changing font size
                    break;
                case 3:
                    label.html(input.attr('data-large')); //changing label
                    input.attr('data-font', 'large'); // changing font size
                    break;

                    //default state
                default:
                    label.html(input.attr('data-medium'));
                    input.attr('data-font', 'normal');
                    break;
            }
            //apply style to the selected range
            this.sheet.textContent = accWidget.setStyle(curVal);
            // reinit reInitAccordion
            this.reInitAccordion()
            setTimeout(()=>{
                this.reInitAccordion()
            },1000)
            
            
        }

        setStyle(val, blindMode) {
            blindMode = blindMode || getCookie("blind-mode");
            const prefs = ['webkit-slider-runnable-track', 'moz-range-track', 'ms-track'];
            let value = (val - 1) * 50,
                style = '';

            let body = $('body');
            let color = '#171c8f'


            // special statements for accessibility
            switch (blindMode) {
                case 'blind-color':
                case 'blind-green':
                    color = '#fff';
                    break;
                case 'blind-red':
                    color = '#ffff00';
                    break;
                default:
                    color = '#171c8f'
            }

            let trackColor = blindMode != null && blindMode != 'blind-default'?  '#8F8F8F' : '#edeeef';
            
            let isArabic = $('html').attr('lang') == 'ar';
            let gradientDirection = isArabic? 'to left': 'to right';

              style += this.rangeInput.selector + ' {background: linear-gradient('+gradientDirection+', ' + color + ' 0%, ' + color + ' ' + value + '%, #fff ' + value + '%, #fff 100%)}';

             // Change background gradient
             for (var i = 0; i < prefs.length; i++) {
                 style += this.rangeInput.selector + ' input::-' + prefs[i] + '{background: linear-gradient('+gradientDirection+', ' + color + ' 0%, ' + color + ' ' + value + '%, '+trackColor+' ' + value + '%, '+trackColor+' 100%)}';
             }

             

            return style;

        }

        reInitAccordion() {
            let acc = document.getElementsByClassName("accTitle");

            $.each(acc, function (i, l) {

                // calculates on accessibility option changes
                if ($(this).hasClass("active")) {
                    let panel = acc[i].nextElementSibling;
                    panel.style.maxHeight = panel.scrollHeight + "px";
                    
                }
            });

        }

    }

    let accWidget = new AccessibilityWidget();

})