$(function(){
    class MobileModal{
        constructor(){
            this.MobileModalTrigger()
        }

        MobileModalTrigger() {
            const trigger = document.querySelectorAll('.md-trigger');
            const mdclose = document.querySelectorAll('.md-close')

            // open
            if (trigger) {
                for (let i = 0; i < trigger.length; i++) {
                    trigger[i].addEventListener('click', function () {
                        const modalId = $(this).attr('data-modal')
                        $(`.md-modal#${modalId}`).addClass('md-show');
                        $('body').addClass('noscroll')
                    })
                }
                
            }

            // close
            if (mdclose) {
                for (let i = 0; i < mdclose.length; i++) {
                    mdclose[i].addEventListener('click', function () {
                        $('.md-modal').removeClass('md-show');
                        $('body').removeClass('noscroll')
                    })
                }
            }
        }
    }

    new MobileModal()
})