$(function(){
    class CommonNavWidget{
        constructor(){
            this.dropdown = $('header .header-dropdown');
            this.navTriggers = $('.m-acc.md-trigger, .m-lang.md-trigger');
            
            this.dropdown.on('click', '.md-close', function(){commonNav.mobileAccessClose()});
            this.navTriggers.on('click', function(e) {commonNav.openNavDropdown(e)});
            this.dropdown.on('click', '.container-md', function(e){e.stopPropagation()});
            $(document).on('click', '.menu-btn', this.hideFadeOverlay)

            this.init();
        }

        init(){
            $('body').click(function () {
                //mobile removing active styling
                $("header .accessibility").removeClass("active");
                if(this.dropdown)
                    this.dropdown.hide();
            });
        }

        openNavDropdown(e){
            e.stopPropagation();

            let element = $('.md-modal#'+e.target.getAttribute('data-modal'));
            element.removeAttr('style');
        }

        mobileAccessClose(){            
           $('.m-acc.md-trigger').removeClass('active');
        }

        hideFadeOverlay(){
            if( !$('.menu-btn').hasClass('active') ) {
                $('.fade-body').fadeOut();
            }
        }


    }
    let commonNav = new CommonNavWidget();
})