$(function(){
    $(".search").click(function () {
        
        if ($(this).hasClass('active')) {
            $(this).find('span').text('');
            $(this).find('img').attr('src', '/rta-assets/img/sprite.svg#close');
        } else {
            $(this).find('span').text('');
            $(this).find('img').attr('src', '/rta-assets/common/img/latest-img/search-icon-en.png');
        }
    });
})