$(function(){

    class QuickWidget{
        
        constructor(){
            $('.toggleQwidget').on('click', this.toggleWidget)
            $(document).on('click', '.triggerSpinner', this.triggerSpinner)
            $(document).on('keyup', '.makeUppercase', this.inputUppercase)
            $(document).on('click', '.closeHwidget, .toggleQwidget', this.resetWidget)
        }

        toggleWidget(){
            const $this = $(this);
            const widgetItem = '.qwidget__item';

            // close widget results section incase if its open
            const resultsId = 'qwidget__results';
            const formId = 'qwidget__form';
            setTimeout(() => {
                // remove 'hide class for form 
                $(`#${formId}, .${formId}`).removeClass('hide');

                // add 'hide' class from results 
                $(`#${resultsId}, .${resultsId}`).addClass('hide');
            }, 1000)
            // end

            if ( !$this.parents(widgetItem).hasClass('is-open') ) {
                $(widgetItem).removeClass('is-open')
                $(widgetItem).addClass('is-collapse')
                $this.parents(widgetItem).removeClass('is-collapse').addClass('is-open');
            }
            else {
                $this.parents(widgetItem).removeClass('is-open');
                $(widgetItem).removeClass('is-collapse')
            }
            
        }

        // trigger spinner
        triggerSpinner() {
            const $this = $(this)
            const spinnerId = $this.attr('data-spinner');
            const resultsId = $this.attr('data-results');
            const formId = $this.attr('data-form');

            $(`#${spinnerId}, .${spinnerId}`).removeClass('hide');

            setTimeout(() => {
                $(`#${spinnerId}, .${spinnerId}`).addClass('hide');

                // add 'hide class for form 
                $(`#${formId}, .${formId}`).addClass('hide');

                // remove 'hide' class from results 
                $(`#${resultsId}, .${resultsId}`).removeClass('hide');
            }, 2000)
        }

        // Reset widget view 
        resetWidget(){
            const $this = $(this)
            const resultsId = $this.attr('data-results');
            const formId = $this.attr('data-form');

            setTimeout(() => {
                // remove 'hide class for form 
                $(`#${formId}, .${formId}`).removeClass('hide');

                // add 'hide' class from results 
                $(`#${resultsId}, .${resultsId}`).addClass('hide');
            }, 1000)

        }

        // Make input character uppercase
        inputUppercase(){
            $(this).val($(this).val().toUpperCase());
        }



    }

    new QuickWidget();
})